<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      selected: ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.con-view{
  margin-top: 10px;
  min-height: 200px;
}
</style>



