<template>
    <div>
       <mt-search v-model="value"  cancel-text="取消"placeholder="搜索"></mt-search>
       <div class="hotword">
        <h3>热门搜索</h3>
         <ul>
           <li class="tag-s" v-for="key,i  in hotwords"  :key="i">
             <span>{{key.k}}</span>
           </li>
         </ul>
       </div>
    </div>
</template>

<script>
//https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg?g_tk=5381&uin=0&format=jsonp&jsonpCallback=callback&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_=1514887002288
export default {
  data () {
    return {
      value:'',
      hotwords:[]
    }
  },
  methods:{
    gethotword(){
      let _this=this;
      $.ajax({
              type: "get",
              async: false,
              url:"https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg?g_tk=5381&uin=0&format=jsonp&jsonpCallback=callback&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_=1514887002288",
              dataType: "jsonp",
              jsonp: "callback",
              jsonpCallback: "callback",
              scriptCharset: 'GBK',//设置编码，否则会乱码
              success: function(res) {
                _this.hotwords=res['data']['hotkey'];
                //console.log(res);
              },
              error: function() {
                alert('fail');
              }
            });
    },
    getsearch(){

    }
  },
  mounted(){
        this.$nextTick(function (){
                this.gethotword()
          })
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.mint-search{
  height: 70px;
}
.tag-s{
   display: inline-block;
    font-size: 14px;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
    color: #000;
    border: 1px solid rgba(0,0,0,.6);
    border-radius: 99px;
    word-break: keep-all;
    margin-bottom: 10px;
    margin-right: 14px;
}
.hotword{
  margin:8px;
}
</style>



